import useParallaxScroll from 'hooks/useParallaxScroll';
import { useRef } from 'react';

const ParallaxScroll = ({ children, className, animationSetting }) => {
  const itemRef = useRef(null);

  useParallaxScroll(itemRef, {
    ...animationSetting,
    scrollTrigger: {
      ...animationSetting.scrollTrigger,
      // // start: 'top bottom', // 요소의 상단이 뷰포트 하단에 도달할 때
      // // end: 'bottom 90%', // 요소의 하단이 뷰포트 높이의 90%에 도달할 때
      // start: 'top+=150% bottom', // 요소의 상단이 뷰포트 하단 아래로 나갔을 때 시작
      // end: 'top+=250% bottom', // 요소의 상단이 뷰포트 하단 아래로 더 나갔을 때 끝
      scrub: true, // 스크롤에 따라 자연스럽게 애니메이션
    },
  });

  return (
    <div ref={itemRef} className={className}>
      {children}
    </div>
  );
};

export default ParallaxScroll;
