import { useEffect, useRef, useState } from 'react';
import Animation, { AnimationClassName } from './Animation';
import { useNavigate } from 'react-router-dom';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import ParallaxScroll from './ParallaxScroll';
import Whatamesh from './Whatamesh';
import useSectionHeight from 'hooks/useSectionHeight';
gsap.registerPlugin(ScrollTrigger);

const Section1 = () => {
  const navigate = useNavigate();

  const sectionHeight = useSectionHeight('section-1');

  const moveInquiry = () => {
    navigate('/inquiry');
  };

  return (
    <section id="section-1">
      <div className="text-box">
        <p>대충 이야기해도</p>
        <p>찰떡같이 알아서 하는 ALF</p>
      </div>
      <div className="section-btn" onClick={moveInquiry}>
        <p className="p">문의하기</p>
        <p className="p-hover">문의하기</p>
      </div>
      <Whatamesh height={sectionHeight} />
    </section>
  );
};

