import { useState, useEffect } from 'react';

const useSectionHeight = (sectionId) => {
  const [height, setHeight] = useState(0);

  const updateHeight = () => {
    const section = document.getElementById(sectionId); // sectionId를 받아서 DOM 요소 가져오기

    if (section) {
      const sectionHeight = section.offsetHeight; // 높이 계산
      setHeight(sectionHeight); // 상태 업데이트
    }
  };

  useEffect(() => {
    updateHeight(); // 초기 높이 설정

    // 창 크기 변경 시 높이 업데이트
    window.addEventListener('resize', updateHeight);
    window.addEventListener('scroll', updateHeight);

    // 컴포넌트 언마운트 시 이벤트 리스너 제거
    return () => {
      window.removeEventListener('resize', updateHeight);
      window.removeEventListener('scroll', updateHeight);
    };
  }, [sectionId]); // sectionId가 변경되면 다시 실행

  return height;
};

export default useSectionHeight;
